      description: 'A personal portfolio website showcasing projects and skills. Built with React and styled-components.',
      image: 'https://via.placeholder.com/350x200',
      tags: ['React', 'Styled Components'],
      category: 'web',
      liveLink: 'https://example.com',
      githubLink: 'https://github.com',
